import React from "react";
import { TodoContext } from "../../TodoContext";
import './TodoForm.css'

 function TodoForm(){

    const {addTodo, setOpenModal } = React.useContext(TodoContext)
    const [newTodoValue,setNewTodoValue] = React.useState('');

    const onCancel= ()=>{
        //TODO
        setOpenModal(false)
    };


    const onSubmit = (event)=>{
        event.preventDefault();
        addTodo(newTodoValue);
        setOpenModal(false)

    }

    const onChange = (event)=>{
       const texto = event.target.value
        setNewTodoValue(texto);
    }


     return(
         <form onSubmit={onSubmit}>
             <label htmlFor="">Escribe tu nuevo TODO</label>
             <textarea value={newTodoValue} onChange={onChange} placeholder="Cortar la Cebolla para el almuerzo" ></textarea>
             <div className="TodoForm-buttonContainer">
                 <button className="TodoForm-button TodoForm-button--cancel" type="button" onClick={onCancel} >Cancelar</button>
                 <button className="TodoForm-button TodoForm-button--add" type="submit">Añadir</button>
             </div>


         </form>
     )
 }

 export {TodoForm};